.SideDrawer{
    position: fixed;
    width: 300px;
    max-width: 250px;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: #fff;
    padding: 32px 16px;
    box-sizing:  border-box;
    transition: transform .3s ease-out;
}

@media (min-width: 500px){
    .SideDrawer{
        display: none;
    }
}

.Open{
    transform: translateX(0);
}
.Close{
    transform:  translateX(-100%);
}